<template>
	<div class="sub-container notice-page">
		<h2 class="board-title">공지사항</h2>
		<div class="board-list marB-30">
			<router-link
				class="board-item"
				v-for="item in boardList"
				:key="item.title"
				:to="{ name: `NoticeDetail`, params: { id: item.id } }"
			>
				<span class="title">{{ item.title }}</span>
				<span class="date">{{ item.date }}</span>
			</router-link>
		</div>
		<Pagination
			v-if="totalPage !== -1 && boardList"
			:listRowCt="15"
			:pageLinkCt="5"
			:totalCount="totalPage"
			:url="'/notice'"
		/>
	</div>
</template>

<script>
import { fetchNotice, fetchNoticeTotalCount } from '@/api/notice';
import Pagination from '@/components/common/Pagination';

export default {
	components: { Pagination },
	created() {
		let page = this.$route.params.page - 1 || 0;
		this.fetchNotice(page);
		this.fetchNoticeTotalCount();
	},
	data() {
		return {
			current: 1,
			isLoading: false,
			boardList: [],
			totalPage: -1,
		};
	},
	methods: {
		async fetchNotice(page) {
			const { data } = await fetchNotice(page);
			this.boardList = data;
		},
		async fetchNoticeTotalCount() {
			this.isLoading = true;
			try {
				const { data } = await fetchNoticeTotalCount();
				this.totalPage = data.totalCount;
			} catch (e) {
				alert(e);
			}
		},
	},
	watch: {
		$route(to, from) {
			if (to !== from) {
				this.currentPage = this.$route.params.page;
				this.fetchNotice(this.currentPage - 1);
			}
		},
	},
};
</script>

<style scoped lang="scss">
.sub-container.notice-page {
	width: 700px;

	.board-item {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 65px;
		border-bottom: 2px solid #e6e6e6;
	}

	.board-item:hover {
		cursor: pointer;
	}
}
</style>
